<script>

export default {
  name: "DarkModeToggle",
  data() {
    const darkModeQuery = window.matchMedia("(prefers-color-scheme: dark)");
    return {
      darkModeQuery: darkModeQuery,
      darkModeEnabled: darkModeQuery.matches,

      prefersDarkScheme: darkModeQuery.matches,
    }
  },
  mounted() {
    this.darkModeQuery.addEventListener("change", () => {
      this.prefersDarkScheme = this.darkModeQuery.matches;
      this.darkModeEnabled = this.prefersDarkScheme;

    })

    // this.togglePosition = this.prefersDarkScheme;
  },
  methods: {
    toggle() {
      if (this.prefersDarkScheme) {
        document.body.classList.toggle("light-theme");
      } else {
        document.body.classList.toggle("dark-theme");
      }
      this.darkModeEnabled = this.darkModeQuery.matches;

    }
  },
  computed: {
    // prefersDarkScheme: {
    //   get() {
    //     let darkMode = window.matchMedia("(prefers-color-scheme: dark)").matches;
    //     if(darkMode === undefined){
    //       darkMode =  true;
    //     }
    //     return darkMode
    //   }
    // },
  },
}
</script>

<style scoped>

label {

  width: 6rem;
  height: 2.5rem;
  position: relative;
  display: block;
  /*background: #ebebeb;*/
  background: rgba(235, 235, 235, 0.1);
  border-radius: 2.5rem;
  box-shadow: inset 0 0.05rem 0.1rem rgba(0, 0, 0, 0.4), inset 0 -0.05rem 0.1rem rgba(255, 255, 255, 0.3);
  cursor: pointer;
  transition: 0.3s;
}

label:after {
  content: "";
  width: 2.1rem;
  height: 2.1rem;
  position: absolute;
  top: 0.2rem;
  left: 0.2rem;
  background: linear-gradient(180deg, #ffcc89, #d8860b);
  border-radius: 1.8rem;
  box-shadow: 0 0.1rem 0.2rem rgba(0, 0, 0, 0.2);
  transition: 0.3s;
}

input {
  width: 0;
  height: 0;
  visibility: hidden;
}

input:checked + label {
  /*background: #242424;*/
  background: rgba(36, 36, 36,0.1);
}

input:checked + label:after {
  left: 5.8rem;
  transform: translateX(-100%);
  background: linear-gradient(180deg, #777, #3a3a3a);
}

label:active:after {
  width: 45%;
}

label svg {
  position: absolute;
  height: 1.7rem;
  top: 0.4rem;
  z-index: 100;
}

label svg.sun {
  left: 0.4rem;
  fill: #fff;
  transition: 0.3s;
}

label svg.moon {
  left: 3.9rem;
  fill: #7e7e7e;
  transition: 0.3s;
}

input:checked + label svg.sun {
  fill: #7e7e7e
}

input:checked + label svg.moon {
  fill: #fff
}
#toggle-container{
  position: fixed;
  top: 4rem;
  right: 2.5rem;
  /*transform: rotate(90deg);*/
}
@media (max-width: 800px) {
  #toggle-container {
    top: 6rem;
    right: 0rem;
    transform: rotate(90deg);
  }
  label svg.sun,
  label svg.moon {
    transform: rotate(-90deg);
  }
}

@media (max-width: 700px) {
  #toggle-container {
    display: none;
  }
}
</style>